import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import '../../styles/ChangePw.scss';

function ChangePw({ setIsLoggedIn }) {
    const navigate = useNavigate();
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [valText, setValText] = useState({});
    const [regulFail, setRegulFail] = useState({
        userPSW:
            ', ,   8 15  .',
        userPSWCH: '  .',
    });

    const validation = (e) => {
        if (e.target.id === 'userPSW') {
            setPassword(e.target.value);
        } else {
            setConfirmPassword(e.target.value);
        }
        
        console.log(valText.id);
        const { id, value } = e.target;
        if (
            /^(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*])[a-z0-9!@#$%^&*]{8,15}$/g.test(
                value
            )
        ) {
            setValText((prev) => ({
                ...prev,
                [id]: null,
            }));
        } else {
            setValText((prev) => ({
                ...prev,
                [id]: regulFail[id],
            }));
        }
    };

    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    };

    const handleConfirmPasswordChange = (e) => {
        setConfirmPassword(e.target.value);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (password !== confirmPassword) {
            // rePSW: '  .';
            return;
        }

        try {
            const response = await axios.post('/user/updatepw', { password });
            console.log(response.data); //   
            alert('  ');
            setIsLoggedIn(false);
            navigate('/'); //      
        } catch (error) {
            console.error(error);
            alert('  ');
        }
    };

    return (
        <div className="changePwContainer">
            <h1> </h1>
            <form className="forminfo" onSubmit={handleSubmit}>
                <table className="userinfoTable">
                    <tbody>
                        <tr>
                            <th> </th>
                            <td>
                                <input
                                    type="password"
                                    minLength="6"
                                    maxLength="16"
                                    name="password"
                                    id="userPSW"
                                    value={password}
                                    onChange={validation}
                                    // onBlur={validation}
                                    required
                                />
                            </td>
                            {valText.userPSW && (
                                <div className="cheakTxt">
                                    {valText.userPSW}
                                </div>
                            )}
                        </tr>
                        <tr>
                            <th> </th>
                            <td>
                                <input
                                    type="password"
                                    minLength="6"
                                    maxLength="16"
                                    name="password"
                                    id="userPSWCH"
                                    value={confirmPassword}
                                    onChange={validation}
                                    // onBlur={validation}
                                    required
                                />
                            </td>
                            {valText.userPSWCH && (
                                <div className="cheakTxt">
                                    {valText.userPSWCH}
                                </div>
                            )}
                        </tr>
                    </tbody>
                </table>
                <div className="modifyinfoBtn">
                    <button type="submit"></button>
                    <button type="button" onClick={() => navigate(-1)}>
                        
                    </button>
                </div>
            </form>
        </div>
    );
}

export default ChangePw;
