import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '../../styles/Adminpage.scss';
import { useNavigate } from 'react-router-dom';

export default function MyInfo() {
    return (
        <div id="userinfo_container">
            <p className="pageTitle"> </p>
            <div id="contents">
                <MyInfoForm />
            </div>
        </div>
    );
}
/////////////////////////////////////////////////////////////////////////////////////////////

export function MyInfoForm() {
    const [loginInfo, setLoginInfo] = useState(null); // db    
    const navigate = useNavigate();

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const response = await axios.get('/user/userinfo');
            setLoginInfo(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    //     
    if (loginInfo === null) {
        return (
            <div style={{ fontWeight: 'bold', fontSize: 30, height: 600 }}>
                Loading...
            </div>
        );
    }

    //   - 
    const firstNum = loginInfo.phone ? loginInfo.phone.substring(0, 3) : '';
    const secondNum = loginInfo.phone ? loginInfo.phone.substring(4, 8) : '';
    const lastNum = loginInfo.phone ? loginInfo.phone.substring(9) : '';

    //  input value 
    const handleChange = (e) => {
        const { name, value } = e.target;
        setLoginInfo((prevLoginInfo) => ({
            ...prevLoginInfo,
            [name]: value,
        }));
    };

    //  value   ( )
    const handleFormSubmit = async (e) => {
        e.preventDefault();
        console.log(loginInfo); //  loginInfo  
        try {
            const response = await axios.post('/user/update', loginInfo, {
                headers: {
                    'Content-Type': 'application/json',
                },
            });
            console.log(response.data); //   
            alert('  ');
            navigate('/mypage');
            //   
        } catch (error) {
            console.error(error);
            alert('  ');
        }
    };

    return (
        <form onSubmit={handleFormSubmit} className="forminfo" method="post">
            <table className="userinfoTable">
                <tbody>
                    <tr>
                        <th></th>
                        <td>
                            <input
                                type="text"
                                minLength="4"
                                maxLength="16"
                                name="id"
                                id="userID"
                                value={loginInfo.id}
                                onChange={handleChange}
                                required
                            />
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                            <input
                                type="password"
                                minLength="6"
                                maxLength="16"
                                name="password"
                                id="userPSW"
                                value="**************"
                                readOnly
                            />
                            <button
                                type="button"
                                className="btnstyle"
                                onClick={() => {
                                    navigate('/changepw');
                                }}
                            >
                                 
                            </button>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                            <input
                                type="text"
                                minLength="2"
                                maxLength="10"
                                name="name"
                                id="userName"
                                value={loginInfo.name}
                                onChange={handleChange}
                            />
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td className="postalinput">
                            <input
                                type="text"
                                name="postal"
                                size="10px"
                                value={loginInfo.postal}
                                onChange={handleChange}
                            />
                            <button></button>
                            <br />
                            <input
                                type="text"
                                name="address_1"
                                size="50"
                                value={loginInfo.address_1}
                                onChange={handleChange}
                            />
                            <span></span>
                            <br />
                            <input
                                type="text"
                                name="address_2"
                                size="50"
                                id="address"
                                value={loginInfo.address_2}
                                onChange={handleChange}
                            />
                            <span></span>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td className="phoneinput">
                            <input
                                type="text"
                                name="phone"
                                size="5px"
                                id="firstNum"
                                value={firstNum}
                                onChange={handleChange}
                            />
                            -
                            <input
                                type="text"
                                name="phone"
                                size="5px"
                                id="secondNum"
                                value={secondNum}
                                onChange={handleChange}
                            />
                            -
                            <input
                                type="text"
                                name="phone"
                                size="5px"
                                id="thirdNum"
                                value={lastNum}
                                onChange={handleChange}
                            />
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                            <input
                                type="email"
                                name="email"
                                size="30px"
                                id="email"
                                value={loginInfo.email}
                                onChange={handleChange}
                            />
                        </td>
                    </tr>
                </tbody>
            </table>
            <div className="modifyinfoBtn">
                <button type="submit"></button>
                <button
                    type="button"
                    onClick={() => {
                        navigate(-1);
                    }}
                >
                    
                </button>
            </div>
        </form>
    );
}
