import React, { useState, useEffect } from 'react';
import '../../styles/Mypage.scss';
import Category from './Category';
import { Link } from 'react-router-dom';
import axios from 'axios';

export default function Mypage() {
    const [loginInfo, setLoginInfo] = useState([]);

    // db      
    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const response = await axios.get('/user/userinfo');
            setLoginInfo(response.data);
        } catch (error) {
            console.error(error);
        }
    };
    return (
        <div id="mypage_container">
            <p className="pageTitle"></p>
            <div id="contents">
                <div className="myPageBox">
                    <div className="profileBox">
                        <div className="profile part1">
                            <div>
                                <span style={{ fontWeight: 'bold' }}>
                                    {loginInfo.name}
                                </span>
                                &nbsp;
                            </div>
                            <div>
                                <Link to="#"> </Link>
                                <Link to="/myinfo"> </Link>
                            </div>
                        </div>
                        <div className="profile part2">
                            <div></div>
                            <div>
                                <span>{loginInfo.point}</span> Point
                            </div>
                        </div>
                        <div className="profile part4">
                            <Link to="#">
                                <div> </div>
                            </Link>
                        </div>
                    </div>
                </div>

                <div className="shipmentBorder">
                    <p> </p>
                    <div className="shipmentBox">
                        <div className="ship Course1">
                            <p></p>
                            <div>0</div>
                        </div>
                        <div className="ship Course2">
                            <p></p>
                            <div>0</div>
                        </div>
                        <div className="ship Course3">
                            <p></p>
                            <div>0</div>
                        </div>
                        <div className="ship Course4">
                            <p></p>
                            <div>0</div>
                        </div>
                    </div>
                </div>
                <Category />
            </div>
        </div>
    );
}
