import React, { useEffect, useState } from 'react';
import '../../styles/JoinForm.scss';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Popup from './Popup';
import PopupPost from './PopupPost';

function JoinForm() {
    return (
        <>
            <div id="joinform_container">
                <p className="pageTitle"></p>
                <div className="contents">
                    <JoinBox />
                </div>
            </div>
        </>
    );
}

// DB   (form   db )

const JoinBox = () => {
    //    
    const [userData, setUserData] = useState({});
    //   
    const [phoneNum, setPhoneNum] = useState({
        firstNum: '',
        secondNum: '',
        lastNum: '',
    });
    const navigate = useNavigate();

    //       - 1/2
    const phoneFunc = (e) => {
        //     input   
        const { name, value } = e.target;
        setPhoneNum((phoneData) => ({
            ...phoneData,
            [name]: value,
        }));
    };

    //       - 2/2
    useEffect(() => {
        //            
        setUserData((data) => ({
            ...data,
            phone: `${phoneNum.firstNum}-${phoneNum.secondNum}-${phoneNum.lastNum}`,
        }));
    }, [phoneNum]);

    //    
    const [sucLeng, setSucLeng] = useState();
    const input = document.querySelectorAll('input');
    //   
    const dataSubmit = async (e) => {
        e.preventDefault();

        setSucLeng(
            Object.values(completeVal).filter((value) => value != null).length
        );

        if (sucLeng >= input.length - 1) {
            try {
                console.log(userData);
                const response = await axios.post('/user/join', userData);
                alert(' .');
                navigate('/login');
            } catch {
                alert(' .  .');
            }
        } else {
            alert(' .  .');
        }
    };

    //      
    const [completeVal, setCompleteVal] = useState({});
    //    
    const [passReg, setPassReg] = useState();

    //  
    useEffect(() => {
        setPassReg(new RegExp(userData.password));
    }, [userData]);

    //  
    useEffect(() => {
        setRegul((prev) => ({
            ...prev,
            rePSW: passReg,
        }));
    }, [passReg]);
    const [regul, setRegul] = useState({
        userID: /^(?=.*[a-z])(?=.*[0-9])[a-z0-9]{8,15}$/g,
        userPSW:
            /^(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*])[a-z0-9!@#$%^&*]{8,15}$/g,
        rePSW: passReg,
        passRequest: /^(?!.*select).*$/,
        ckpwa: /^(?=.{3,20}$).*$/,
        userName: /^[-]{2,5}$/,
        address: /^.{3,50}$/,
        firstNum: /^(?!$).+$/,
        secondNum: /^\d{3,4}$/,
        thirdNum: /^\d{4}$/,
        email: /^[\w.-]+@[a-zA-Z_-]+?\.[a-zA-Z]{2,3}$/,
    });

    //      
    const [regulFail, setRegulFail] = useState({
        userID: ',   8  15  .',
        userPSW:
            ', ,   8 15  .',
        rePSW: '  .',
        passRequest: '  .',
        ckpwa: '3  20   .',
        userName: ' 2  5  .',
        address: '3  50  .',
        firstNum: '   .',
        secondNum: '   .',
        thirdNum: '   .',
        email: '   .',
    });

    //    
    const [valText, setValText] = useState({
        address_1: ' .',
        postal: ' .',
    });
    //   
    const validation = (e) => {
        console.log(valText.id);
        const { id, name, value } = e.target;
        if (value === '') {
            setValText((prev) => ({
                ...prev,
                [id]: '  .',
            }));
        } else if (regul[id].test(value)) {
            setValText((prev) => ({
                ...prev,
                [id]: ' .',
            }));
            setCompleteVal((prev) => ({
                ...prev,
                [id]: '  ',
            }));
            //   
            setUserData((data) => ({
                ...data,
                [name]: value,
            }));
        } else {
            setValText((prev) => ({
                ...prev,
                [id]: regulFail[id],
            }));
        }
    };

    // ID  
    const [counts, setCounts] = useState(false);
    const checkid = async (e) => {
        e.preventDefault();
        if (/^(?=.*[a-z])(?=.*[0-9])[a-z0-9]{8,15}$/g.test(userData.id)) {
            await axios('/user/checkid')
                .then((response) => {
                    if (response.data.includes(userData.id)) {
                        setValText((prev) => ({
                            ...prev,
                            userID: '  .',
                        }));
                    } else {
                        setCounts(true);
                        setValText((prev) => ({
                            ...prev,
                            userID: '  .',
                        }));
                        setCompleteVal((prev) => ({
                            userID: '  ',
                        }));
                    }
                })
                .catch();
        } else {
            setValText((prev) => ({
                ...prev,
                id: ',   8  15  .',
            }));
        }
    };

    const [isPopupOpen, setIsPopupOpen] = useState(false);
    const [popupPostData, setPopupPostData] = useState([]);
    //  
    const openPostCode = (e) => {
        e.preventDefault();
        setIsPopupOpen(true);
    };

    //  
    const closePostCode = () => {
        setIsPopupOpen(false);
    };

    //    
    const handlePopupData = (data) => {
        setPopupPostData(data);
    };

    // DB      
    useEffect(() => {
        setUserData((prev) => ({
            ...prev,
            postal: popupPostData[1],
            address_1: popupPostData[0],
        }));
    }, [popupPostData]);

    return (
        <>{isPopupOpen && <div style={{ width: "100vw", height: "100vh", backgroundColor: 'white', opacity: ".7", zIndex: "2", position: "absolute" }}></div>}
            <form onSubmit={dataSubmit} className="joinbox" method="post">
                <p>
                    <span>*</span>   
                </p>
                <figure>
                    <figcaption></figcaption>
                    <table>
                        <colgroup>
                            <col />
                            <col />
                        </colgroup>

                        <tbody>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        minLength="4"
                                        maxLength="16"
                                        name="id"
                                        id="userID"
                                        onBlur={counts ? null : validation}
                                        readOnly={counts}
                                        style={
                                            counts
                                                ? { border: '2px solid black' }
                                                : null
                                        }
                                    />
                                    <button onClick={checkid} disabled={counts}>
                                        
                                    </button>
                                    {valText.userID && (
                                        <div>{valText.userID}</div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="password"
                                        minLength="6"
                                        maxLength="16"
                                        name="password"
                                        id="userPSW"
                                        onBlur={validation}
                                    />
                                    <span></span>
                                    {valText.userPSW && (
                                        <div>{valText.userPSW}</div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                      <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="password"
                                        minLength="6"
                                        maxLength="16"
                                        id="rePSW"
                                        onBlur={validation}
                                    />
                                    {valText.rePSW && (
                                        <div className="confirmPSW notice">
                                            {valText.rePSW}
                                        </div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                       <span>*</span>
                                </th>
                                <td>
                                    <select
                                        id="passRequest"
                                        name="password_q"
                                        onBlur={validation}
                                    >
                                        <option value="select">
                                            .
                                        </option>
                                        <option value="pswQ1">
                                               ?
                                        </option>
                                        <option value="pswQ2">
                                              ?
                                        </option>
                                        <option value="pswQ3">
                                               1?
                                        </option>
                                        <option value="pswQ4">
                                                ?
                                        </option>
                                        <option value="pswQ5">
                                                
                                            ?
                                        </option>
                                        <option value="pswQ6">
                                               ?
                                        </option>
                                        <option value="pswQ7">
                                                 
                                            ?
                                        </option>
                                        <option value="pswQ8">
                                                ?
                                        </option>
                                        <option value="pswQ9">
                                                ?
                                        </option>
                                        <option value="pswQ10">
                                                
                                            ?
                                        </option>
                                        <option value="pswQ11">
                                                ?
                                        </option>
                                        <option value="pswQ12">
                                                
                                             ?
                                        </option>
                                        <option value="pswQ13">
                                                 ?
                                        </option>
                                        <option value="pswQ14">
                                                ?
                                        </option>
                                        <option value="pswQ15">
                                              ?
                                        </option>
                                    </select>
                                    {valText.passRequest ===
                                    ' .' ? null : (
                                        <div>{valText.passRequest}</div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                       <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        name="password_a"
                                        size="70px"
                                        id="ckpwa"
                                        onBlur={validation}
                                    />
                                    {valText.ckpwa ===
                                    ' .' ? null : (
                                        <div>{valText.ckpwa}</div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        minLength="2"
                                        maxLength="10"
                                        name="name"
                                        id="userName"
                                        onBlur={validation}
                                    />
                                    {valText.userName ===
                                    ' .' ? null : (
                                        <div>{valText.userName}</div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        name="postal"
                                        size="10px"
                                        value={popupPostData[1]}
                                        onChange={validation}
                                        readOnly
                                    />
                                    <button onClick={openPostCode}>
                                        
                                    </button>

                                    <br />
                                    <input
                                        type="text"
                                        name="address_1"
                                        size="50"
                                        value={popupPostData[0]}
                                        onChange={validation}
                                        readOnly
                                    />
                                    <span></span>
                                    <br />
                                    <input
                                        type="text"
                                        name="address_2"
                                        size="50"
                                        id="address"
                                        onBlur={validation}
                                    />
                                    <span></span>

                                    {valText.address ===
                                    ' .' ? null : (
                                        <div>{valText.address}</div>
                                    )}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <select
                                        id="firstNum"
                                        name="firstNum"
                                        onChange={phoneFunc}
                                        onBlur={validation}
                                    >
                                        <option value=""></option>
                                        <option value="010">010</option>
                                        <option value="011">011</option>
                                        <option value="016">016</option>
                                        <option value="017">017</option>
                                        <option value="018">018</option>
                                        <option value="019">019</option>
                                    </select>
                                    -
                                    <input
                                        type="text"
                                        name="secondNum"
                                        minLength="3"
                                        maxLength="4"
                                        size="5px"
                                        id="secondNum"
                                        onChange={phoneFunc}
                                        onBlur={validation}
                                    />
                                    -
                                    <input
                                        type="text"
                                        name="lastNum"
                                        minLength="4"
                                        maxLength="4"
                                        size="5px"
                                        id="thirdNum"
                                        onChange={phoneFunc}
                                        onBlur={validation}
                                    />
                                    {valText.firstNum ===
                                        '  .' ||
                                    valText.secondNum ===
                                        '  .' ||
                                    valText.secondNum ===
                                        '  .' ? (
                                        <div>
                                               .
                                        </div>
                                    ) : valText.firstNum ===
                                          '   .' ||
                                      valText.secondNum ===
                                          '   .' ||
                                      valText.secondNum ===
                                          '   .' ? (
                                        <div>
                                               .
                                        </div>
                                    ) : null}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="email"
                                        name="email"
                                        size="30px"
                                        id="email"
                                        onBlur={validation}
                                    />
                                    {valText.email ===
                                    ' .' ? null : (
                                        <div>{valText.email}</div>
                                    )}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </figure>
                <div className="joinButton">
                    <button type="submit" onClick={dataSubmit}>
                        
                    </button>
                </div>
            </form>
            <div id="popupDom" style={{
                zIndex: "9999", position: "fixed",
                left: "50%",
                top: "50%",
                transform: "translate(-50%, -50%)"
            }}>
                {isPopupOpen && (
                    <Popup>
                        <PopupPost
                            onPopupData={handlePopupData}
                            onClose={closePostCode}
                        />
                    </Popup>
                )}
            </div>
        </>
    );
};

export default JoinForm;
